import moment from 'moment';
import React from 'react';
import { edit } from '../../../../assets/images/icons';
import { Checkbox } from '../../../common';
import cls from './TaskCard.module.scss';

function TaskCard(props) {
	const { task, openModal = () => { }, setTaskToEdit = () => { }, clicked = () => { }, isEditable, priority } = props;
	const { name, process, status, add_info } = task;

	return (
		<div className={cls['task-card']}>
			{!task.completed_at ? (
				<div className={cls['task-card-container']} style={{ fontWeight: 'bold', color: 'var(--app-color-gray-dark)', padding: '10px' }}>
					<Checkbox llabel={`${priority !== undefined ? `${priority}` : ''} ${name} ${status !== null ? `  (${status})` : ''}`} onChange={clicked} />
					<div style={{ padding: '7px', textJustify: 'left', width: '100%', cursor: 'pointer' }}
						onClick={() => {
							openModal();
							setTaskToEdit();
						}}
					>
						{`${priority !== undefined ? `${priority}` : ''} ${name} ${status !== null ? `  (${status})` : ''}`}
					</div>
					{isEditable && (
						<img
							alt=""
							style={{ cursor: 'pointer' }}
							src={edit}
							onClick={() => {
								openModal();
								setTaskToEdit();
							}}
						/>
					)}
				</div>
			) : (
				<div
					className={cls['task-card-container']}
					style={{ flex: 1 }}
					onClick={() => {
						openModal();
						setTaskToEdit();
					}}
				>
					<p className={cls.clickLabel} style={{ fontWeight: 'bold', color: priority !== undefined ? 'green' : '' }}>{`${name}`}</p>
					<p className={cls.clickLabel}>{moment(task.completed_at).format('DD of MMMM YYYY')}</p>
				</div>
			)}
		</div>
	);
}

export default TaskCard;
